.pop-up {
  &-form {
    background: #F9F9F9;
    padding: 24px 0;
    &__inner {
      width: 442px;
      margin: auto;
    }
    .form-control {
      width: 288px;
    }
  }
  &-title {
    background: $color-primary;
    color: #fff;
    padding: 14px 0;
    span {
      display: inline-block;
    }
    .navbar-brand__white {
      margin-top: 0;
      vertical-align: middle;
    }
    .close {
      margin-right: 18px;
      outline: none;
      transition: $global-transition;
    }
  }

  &-content {
    padding: 30px 40px;

    .form-group label {
      text-align: right;
      vertical-align: top;
      padding-right: 0; 
      line-height: 1;
    }
  }

  
}

.modal-dialog{
  border-radius: 7px;
  overflow: hidden;
}