.integral-mall {
  &-detail {
    padding: 40px 0 80px 0;
    .car-des {
      margin-bottom: 43px;
      &-row {
        display: flex;
        line-height: 25px;
        &:not(:last-child) {
          margin-bottom: 17px;
        }
        .des-row {
          &__left {
            margin-right: 17px;
            flex-shrink: 0;
            color: $color-black;
            font-size: 16px;
          }
          &__right {
            font-size: 14px;
          }
        }
      }
      &--bling {
        color: $color-warning;
        margin-bottom: 8px;
      }
      &--gray {
        color: $color-light-grey;
      }
    }
  }
  &-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    .actions__left {
      .collection {
        margin-right: 56px;
        display: inline-block;
        color: $color-light-grey;
        .icon {
          margin-right: 8px;
          width: 22px;
          height: auto;
        }
      }
      .surplus {
        display: inline-block;
        color: $color-light-grey;
        .num {
          margin-left: 7px;
          color: $color-primary;
        }
      }
    }
    .actions__right {
      display: flex;
      align-items: center;
      .price {
        margin-right: 33px;
        color: $color-black;
        font-size: 16px;
        .icon {
          margin-right: 18px;
          width: 20px;
          height: auto;
        }
        .num {
          margin-left: 7px;
          color: $color-primary;
        }
      }
    }
  }
}
