.user-info-edit {
  padding: 20px 24px;
  background: $color-white;
  border-radius: 8px;
  &__header {
    padding-bottom: 14px;
    color: $color-dark-grey;
    border-bottom: 1px solid #E8EDF2;
    .icon {
      margin-right: 14px;
      width: 24px;
      height: 24px;
      position: relative;
      top: -3px;
      cursor: pointer;
    }
  }
  &__body {
    padding-top: 22px;
    font-size: 14px;
    .select-box {
      display: flex;
      align-items: center;
      .select-major {
        line-height: inherit;
        border: 1px solid #ced4da;
        box-sizing: border-box;
      }
      .select-des {
        margin-left: 7px;
        color: $color-light-grey;
      }
    }
  }
  &__footer {
    padding-bottom: 456px;
    font-size: 12px;
    text-align: center;
    .c-btn {
      &:not(:last-child) {
        margin-right: 25px;
      }
    }
  }
}
