.type-filter{
  background: #fff;
  padding: 30px 20px;
  margin: 0 -5px;
  margin-bottom: 20px;
  font-size: 14px;
  border-radius: 5px;
  &-row {
    margin-bottom: 15px;
    &:last-child{
      margin-bottom: 0;
    }
  }
  label {
    display: inline-block;
    width: 80px;
    text-align: right;
    margin: 0;
    margin-right: 10px;
  }
  ul {
    display: inline-block;
    margin-bottom: 0;
    li {
      display: inline-block;

      a {
        color: #9B9B9B;
        transition: $global-transition;
        margin-right: 10px;
        &:hover, &.active {
          color: $color-primary;
        }
      }
    }
  }
}