.product-card {
  background: #fff;
  margin-bottom: 20px;
  transition: all .2s linear;
  border-radius: 7px;
  &:hover {
    -webkit-box-shadow: 0 15px 30px rgba(0,0,0,0.1);
    box-shadow: 0 15px 30px rgba(0,0,0,0.1);
    -webkit-transform: translate3d(0, -2px, 0);
    transform: translate3d(0, -2px, 0)
  }

  &-img {
    text-align: center;
    height: 250px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      height: 100%;
      display: block;
    }
    img {
      max-height: 100%;
    }
  }

  &-content {
    padding: 14px 32px;
    font-size: 14px;
  }

  &-title {
    font-size: 16px;
    margin-bottom: 5px;
    font-weight: bold;

    .float-right {
      font-size: 14px;
      strong{
        font-weight: normal;
      }
      font-weight: normal;
    }
  }

  &-intro {
    color: $color-medium-grey;
    margin-top: 10px;
    line-height: 20px;
    strong {
      color: #333;
    }
  }

  &-btm {
    margin-top: 10px;
  }

  &-price {
    font-size: 16px;
    strong {
      font-size: 22px;
      padding-right: 5px;
      color: $color-primary;
    }
  }

  &__small {
    .product-card-img {
      height: 135px;
      width: auto;
    }
    .product-card-content {
      .col {
        padding-left: 0;
        padding-right: 0; 
      }
    }
  }
}
.product-card-list {
  .col {
    padding-left: 10px;
    padding-right: 10px; 
  }
}
.top-auction {
  .product-card-img {
    border-bottom: 1px solid #f0f0f0;
    position: relative;
    &::after {
      content: '';
      width: 0;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      background: $color-primary;
      transition: $global-transition;
    }
    &:hover::after {
      width: 100%;
    }
  }
  .product-card-content {
    padding: 12px 16px;
  }
}